import dateFormat from "moment";



class ShowReviews extends Component {



constructor(props) {
super(props)
this.state = {
reviews:[],
message:'',
citySel:localStorage.citySelected,
stateSel:localStorage.stateSelected,
userNameSel:localStorage.userNameSelected,
showOptions:false,
axiosUrl:'https://rate-my-city.herokuapp.com/api/'
}
this.routerChange = this.routerChange.bind(this);

}


routerChange(cty, ste) {
localStorage.setItem("citySelected", cty);
localStorage.setItem("stateSelected", ste);
this.props.history.push("/AddReview");
}



componentDidMount() {
var axiosUrl = 'https://rate-my-city.herokuapp.com/api/';

if ( this.userNameSel === '' ) {
axiosUrl += 'searchCityState';
} else {
axiosUrl += 'searchUsername';
}

var obj = {
city:this.citySel,
state:this.stateSel,
userName:this.userNameSel};
var js = JSON.stringify(obj);

var config = {
// method: 'get',
method: 'post',
// url: axiosUrl,
url: 'https://rate-my-city.herokuapp.com/api/searchCityState',
headers: { 'Content-Type': 'application/json' },
data: js
};

axios(config)
.then(response => {
var res = response.data;
if (res.error) {
this.setState({
message: res.error
});
} else {
this.setState({
message: '',
reviews: res
});
// returnFromSearch(res);
}
})
}

render () {
const {reviews} = this.state;
return (
<body>
<div className="container input-group justify-content-center">
<table className="master-table">
{
reviews.map((rec =>
<>

<tr className="city-separator">
<td>
<span>
{rec.name}, &nbsp;&nbsp; {rec.state}
</span>
</td>
<td>
<span>
<button className="btn btn-sm btn-secondary" onClick={() => this.routerChange(rec.name, rec.state)} type="button">Add Review</button>
</span>
</td>
</tr>
<div className="row justify-content-center">
<div className="col-auto">
<table className="table table-responsive mb-1 mt-0">
<tr className="rating-avg">
<td>Cost:</td>
<td>{rec.averageRating.cost}</td>
<td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
<td>Nature:</td>
<td>{rec.averageRating.nature}</td>
<td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
<td>Culture:</td>
<td>{rec.averageRating.culture}</td>
<td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
<td>Safety:</td>
<td>{rec.averageRating.safety}</td>
<td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
<td>Entertaiment:</td>
<td>{rec.averageRating.entertainment}</td>
<td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
<td>Transportation:</td>
<td>{rec.averageRating.transportation}</td>
<td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
<td>Food:</td>
<td>{rec.averageRating.food}</td>
</tr>
</table>
</div>
</div>

<div>
{rec.ratings.map(function (rate, i) {
return <div key={i} className="box">
<div>
<tr>
<td className="date-time">{rate.userid} wrote in {dateFormat(rate.time).format('MM-DD-YYYY @ hh:mm A')}: </td>
{/* <td className="delete-button">
<button>Delete</button>
</td> */}
</tr>
<tr className="rate-comment">
{rate.description}
</tr>
</div>
<div className="row justify-content-center">
<div className="col-auto">
<table className="table table-responsive mb-1 mt-0">
<tr className="rating-avg">
<td>Cost:</td>
<td>{rate.rating.cost}</td>
<td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
<td>Nature:</td>
<td>{rate.rating.nature}</td>
<td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
<td>Culture:</td>
<td>{rate.rating.culture}</td>
<td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
<td>Safety:</td>
<td>{rate.rating.safety}</td>
<td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
<td>Entertaiment:</td>
<td>{rate.rating.entertainment}</td>
<td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
<td>Transportation:</td>
<td>{rate.rating.transportation}</td>
<td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
<td>Food:</td>
<td>{rate.rating.food}</td>
</tr>
</table>
</div>
</div>
</div>
})}
</div>
</>
))
}
</table>
</div>
</body>

);
}

}

export default withRouter(ShowReviews);