

import React, { Component, useState, useEffect } from 'react';
import { Dropdown, Button, Modal } from 'react-bootstrap';
import { useHistory } from 'react-router-dom';
import { Typeahead } from 'react-bootstrap-typeahead';
import axios from 'axios';
import "bootstrap/dist/css/bootstrap.min.css";
import '../App.css';
import 'react-bootstrap-typeahead/css/Typeahead.css';


function SearchCity() {

const [showOptions, setShowOptions] = useState(false);
const [message, setMessage] = useState('');
const [cities, setCities] = useState({});
const [states, setStates] = useState({});
const [userNames, setUserNames] = useState({});
const history = useHistory();
const [citySel, setCitySel] = useState('');
const [stateSel, setStateSel] = useState('');
const [userNameSel, setUserNameSel] = useState('');


useEffect(() => {
if ( localStorage.accessToken && localStorage.accessToken !== '' ) {
setShowOptions(true);
}
localStorage.setItem("citySelected", '');
localStorage.setItem("stateSelected", '');
localStorage.setItem("userNameSelected", '');
fillDropdowns();
}, [])



function filterBy(option, state) {
if (state.selected.length) {
return true;
}
return option.label.toLowerCase().indexOf(state.text.toLowerCase()) > -1;
}

function citySelected(selectedOpt) {
setCitySel(selectedOpt[0].label);
}

function stateSelected(selectedOpt) {
setStateSel(selectedOpt[0].label);
}

function userNameSelected(selectedOpt) {
setUserNameSel(selectedOpt[0].label);
}

const ToggleButton = ({ isOpen, onClick }) => (
<button
className="toggle-button"
onClick={onClick}
onMouseDown={e => {
e.preventDefault();
}}>
{isOpen ? '▲' : '▼'}
</button>
);

function fillDropdowns() {
// Get Cities
var config = {
method: 'get',
url: 'https://rate-my-city.herokuapp.com/api/listCities',
// url: 'https://jsonplaceholder.typicode.com/users'

};
axios(config)
.then(function (response) {
var res = response.data;
if (res.error) {
setMessage(res.error);
} else {
setMessage(res.error);
setCities(res.map(({ name: label }) => ({ label })))
// setCities(res.map(({ username: label }) => ({ label })))
}
});

// Get States
var config = {
method: 'get',
url: 'https://rate-my-city.herokuapp.com/api/listStates',
// url: 'https://jsonplaceholder.typicode.com/users'

};
axios(config)
.then(function (response) {
var res = response.data;
if (res.error) {
setMessage(res.error);
} else {
setMessage(res.error);
setStates(res.map(({ state: label }) => ({ label })))
// setStates(res.map(({ email: label }) => ({ label })))
}
})

// Get Users
var config = {
method: 'get',
url: 'https://rate-my-city.herokuapp.com/api/listUserName',
// url: 'https://jsonplaceholder.typicode.com/users'

};
axios(config)
.then(function (response) {
var res = response.data;
if (res.error) {
setMessage(res.error);
} else {
setMessage(res.error);
setUserNames(res.map(({ userName: label }) => ({ label })))
// setUserNames(res.map(({ name: label }) => ({ label })))
}
})
}

function handleClickCity () {
history.push("AddReview")
}

function searchReviews () {
localStorage.setItem("citySelected", citySel);
localStorage.setItem("stateSelected", stateSel);
localStorage.setItem("userNameSelected", userNameSel);
history.push("ShowReviews")
}


return (

<div className="input-group d-flex justify-content-center">

<div className="form-group">
<Typeahead
id="dropCity"
filterBy={filterBy}
options={cities}
onChange={citySelected}
placeholder="Search by City ...">
{({ isMenuShown, toggleMenu }) => (
<ToggleButton isOpen={isMenuShown} onClick={e => toggleMenu()} />
)}
</Typeahead>
</div>

<div className="form-group">
<Typeahead
id="dropState"
filterBy={filterBy}
options={states}
onChange={stateSelected}
placeholder="Search by state ...">
{({ isMenuShown, toggleMenu }) => (
<ToggleButton isOpen={isMenuShown} onClick={e => toggleMenu()} />
)}
</Typeahead>
</div>

<div className="form-group">
<Typeahead
id="dropUserName"
filterBy={filterBy}
options={userNames}
onChange={userNameSelected}
placeholder="Search by User ...">
{({ isMenuShown, toggleMenu }) => (
<ToggleButton isOpen={isMenuShown} onClick={e => toggleMenu()} />
)}
</Typeahead>
</div>

<div>
<button className="btn text-white fw-bold btn-outline-dark btn-secondary" onClick={searchReviews}>Search</button>
{showOptions &&
<button className="btn text-white fw-bold btn-outline-dark btn-secondary" onClick={handleClickCity}>Add Review</button>
}
</div>

<div>

<p>&nbsp;</p>
</div>

</div>
)

}

export default SearchCity;
