import React, { useState } from 'react';
import axios from 'axios';
import { useHistory } from 'react-router-dom';
import validator from 'validator';
import '../App.css';

function Settings() {

const history = useHistory();

const [message, setMessage] = useState('');
const [firstName, setFirstName] = useState(localStorage.userFirstName);
const [lastName, setLastName] = useState(localStorage.userLastName);
const [userName, setUserName] = useState(localStorage.userName);
const [userEmail, setUserEmail] = useState(localStorage.userEmail);
const [oldPassword, setOldPassword] = useState(localStorage.userPassword);
const [loginPassword, setLoginPassword] = useState(localStorage.userPassword);
const [confirmPassword, setConfirmPassword] = useState(localStorage.userPassword);

const doSettings= async event => {
event.preventDefault();

var obj = {
userId: localStorage.userId,
firstName: firstName,
lastName: lastName,
userName: userName,
email: userEmail,
password:loginPassword,
confirmpassword: confirmPassword,
jwtToken: localStorage.accessToken
};

var js = JSON.stringify(obj);

var config = {
method: 'post',
url: 'https://rate-my-city.herokuapp.com/api/settings',
headers: { 'Content-Type': 'application/json' },
data: js
};

axios(config)
.then(function (response) {
var res = response.data;
if (res.error && res.error !== '') {
setMessage(res.error);
} else {
setMessage('');
returnFromSettigs(res);
}
})
}

function returnFromSettigs(result) {
localStorage.setItem('accessToken', result.jwtToken.accessToken);
localStorage.setItem('userId', result.id);
localStorage.setItem('userFirstName', result.firstName);
localStorage.setItem('userLastName', result.lastName);
localStorage.setItem('userName', result.userName);
localStorage.setItem('userEmail', result.email);
localStorage.setItem('userPassword', loginPassword.value);

history.push("/");
}

const validatePassword = (e) => {
if (validator.isStrongPassword(e, {
minLength: 8,
minLowercase: 1,
minUppercase: 1,
minNumbers: 1,
minSymbols: 1
})) {
setMessage('')
} else {
setMessage('The password in not strong enough')
}
}

function handleClickCancel() {
history.push("/");
}


return(
<div className="container-fluid h-custom">
<div className="row d-flex justify-content-center align-items-center h-100">
<div className="col-md-8 col-lg-6 col-xl-4 offset-xl-1">
<form >
<div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: '8vh'}}>
<h1>Settings</h1>
</div>

<div className="form-outline mt-4 mb-2">
<input type="text" autoFocus
className="form-control form-control-lg"
value={firstName}
onChange={(e) => {setFirstName(e.target.value)}}
placeholder="Enter First Name"
/>
<label className="form-label">First Name</label>
</div>
<div className="form-outline mb-2">
<input type="text"
className="form-control form-control-lg"
value={lastName}
onChange={(e) => {setLastName(e.target.value)}}
placeholder="Enter Last Name"
/>
<label className="form-label">Last Name</label>
</div>

<div className="form-outline mb-2">
<input type="text"
className="form-control form-control-lg"
value={userName}
onChange={(e) => {setUserName(e.target.value)}}
placeholder="Enter User Name"
/>
<label className="form-label">Username</label>
</div>

<div className="form-outline mb-2">
<input type="password"
className="form-control form-control-lg"
value={oldPassword}
onChange={(e) => {setOldPassword(validatePassword(e.target.value))}}
placeholder="Enter Old Password"
/>
<label className="form-label">Old Password</label>
</div>

<div className="form-outline mb-2">
<input type="password"
className="form-control form-control-lg"
value={loginPassword}
onChange={(e) => {setLoginPassword(e.target.value)}}
placeholder="Enter New Password"
/>
<label className="form-label">New Password</label>
</div>
<div className="form-outline mb-3">
<input type="password"
className="form-control form-control-lg"
value={confirmPassword}
onChange={(e) => {setConfirmPassword(e.target.value)}}
placeholder="Confirm password"
/>
<label className="form-label">Confirm Password</label>
</div>


<div className="form-group row d-flex justify-content-center ">
<div className="col-md-4">
<button type="button" className="btn btn-secondary btn-lg form-control" onClick={doSettings}>Update</button>
</div>

<div className="col-md-4">
<button type="button" className="btn btn-dark btn-lg form-control" onClick={handleClickCancel}>Cancel</button>
</div>

<div className="divider d-flex align-items-center my-4">
<p className="text-center fw-bold mx-3 mb-1">{message}</p>
</div>
</div>

</form>
</div>
</div>
</div>

);
};

export default Settings;